import React, { useState } from "react";
import { Card, Form } from "react-bootstrap";
import { BiEdit } from "react-icons/bi";
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";
import { BiSearch } from "react-icons/bi";
import { RiDeleteBin6Fill } from "react-icons/ri";
import "react-datepicker/dist/react-datepicker.css";
import "react-datepicker/dist/react-datepicker.css";
import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  deleteUser,
  getUserList,
} from "../../../app/features/userManagement/user.slice";
import AdminModal from "./AdminModal";
import FullScreenLoader from "../../../authorization/FullScreenLoader";
import { ConfirmationModal } from "../../../components/Modal/ConfirmationModal";

export default function AdminRole({ setAddAdmin, addAdmin }) {
  const [open, setOpen] = useState(false);
  const [deleteId, setDeleteId] = useState();
  const [statusId, setStatusId] = useState(null);
  const [searchValue, setSearchValue] = useState();
  const userList = useSelector((state) => state);
  const { isLoading } = useSelector((state) => state.userRole);
  const dispatch = useDispatch();

  const selectRow = {
    mode: "checkbox",
  };

  useEffect(() => {
    const finalData = {
      roleId: 4,
      search: searchValue ? searchValue : null,
      statusId: statusId === "true" ? true : statusId === "false" ?  false : null,
    };
    dispatch(getUserList({ finalData }));
  }, [dispatch, searchValue, statusId]);

  function onDeleteSuccess() {
    let finalData = {
      userId: userList.auth.user.userId,
      roleId: 4,
    };
    dispatch(getUserList({ finalData }));
  }

  function deleteAdminRole() {
    const finalData = {
      userId: deleteId,
    };
    dispatch(deleteUser({ finalData, onDeleteSuccess }));
  }

  const Admincolumns = [
    {
      dataField: "userId",
      text: "ID",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "name",
      text: "Admin Name",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1", width: "17%" },
    },
    {
      dataField: "email",
      text: "Email",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "phoneNumber",
      text: "Phone Number",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "status",
      text: "Status",
      sort: false,
      headerStyle: { backgroundColor: "#F1F1F1" },
      formatter: (row, cell) => (
        <p
          className={`mb-0 text-center py-1 custom-width-class ${
            cell?.status === "InActive" ? "inactive-status" : "active-status"
          }`}
        >
          {cell?.status === "InActive" ? "Inactive" : "Active"}
        </p>
      ),
    },
    {
      dataField: "action",
      text: "Action",
      sort: false,
      formatter: (row, cell) => {
        return (
          <>
            <BiEdit
              style={{ color: "#1A1A1A", cursor: "pointer" }}
              onClick={() => setAddAdmin({ data: cell, show: true })}
            />
            <RiDeleteBin6Fill
              style={{ color: "red", cursor: "pointer" }}
              className="ms-3"
              onClick={() => {
                setDeleteId(cell?.userId);
                setOpen(true);
              }}
            />
          </>
        );
      },
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
  ];

  const customTotal = (from, to, size) => (
    <span className="react-bootstrap-table-pagination-total">
      <b> {from} </b> to <b>{to}</b> out of <b>{size} entries</b>
    </span>
  );

  const paginateOptions = {
    paginationSize: 8,
    pageStartIndex: 1,
    alwaysShowAllBtns: true,
    withFirstAndLast: false,
    hideSizePerPage: true,
    showTotal: true,
    paginationTotalRenderer: customTotal,
    disablePageTitle: true,
    sizePerPageList: [
      {
        text: "10",
        value: 10,
      },
    ],
  };
  return (
    <>
      <Card className="user-details-card">
        <div className="px-4 pt-3 d-flex justify-content-between custom-row align-items-center table-header-border">
          <span className="d-flex align-self-center">
            <Form.Control
              onKeyDown={(e) => {
                if (e.key === "Enter") {
                  setSearchValue(e.target.value);
                }
              }}
              onChange={(e) => {
                if (e.key === "Enter") {
                  setSearchValue(e.target.value);
                }
              }}
              type="text"
              placeholder="Search"
              className="mb-3 search-field-spacing"
              aria-label="Search"
            />
            <BiSearch size={22} className="searchbar-icon" />
          </span>
          <div className="d-flex custom-div-row">
            <div className="mb-2">
              <select
                onClick={(e) => setStatusId(e.target.value)}
                className="form-select pe-5"
                aria-label="Default select example"
              >
                <option selected>Status</option>
                <option value="true">Active</option>
                <option value="false">Inactive</option>
              </select>
            </div>
            {/* <div className="d-flex">
              <Dropdown className="custom-dropdown px-1 mb-3">
                <Dropdown.Toggle id="dropdown-basic">Status</Dropdown.Toggle>
                <Dropdown.Menu>
                  <Dropdown.Item href="#/action-1">
                    <Form.Check
                      type="checkbox"
                      id="missed-checkbox"
                      label="Missed"
                    />
                  </Dropdown.Item>
                  <Dropdown.Item href="#/action-2">
                    <Form.Check
                      type="checkbox"
                      id="completed-checkbox"
                      label="Completed"
                    />
                  </Dropdown.Item>
                </Dropdown.Menu>
              </Dropdown>
            </div> */}
          </div>
        </div>
        {/* {userList?.userRole?.isError === true && (
          <p className="text-center">Network Error...</p>
        )} */}
        {isLoading ? (
          <FullScreenLoader />
        ) : (
          <span className="doctor-datatable">
            <BootstrapTable
              columns={Admincolumns}
              data={
                userList?.userRole?.getAllUser?.data
                  ? userList?.userRole?.getAllUser?.data
                  : []
              }
              keyField="userId"
              id="bar"
              pagination={paginationFactory(paginateOptions)}
              // striped
              bordered={false}
              wrapperClasses="table-responsive"
              selectRow={selectRow}
              sort={{ dataField: "userId", order: "desc" }}
            />
          </span>
        )}
      </Card>
      <ConfirmationModal
        show={open}
        onHide={() => setOpen(false)}
        heading={"Delete Admin"}
        title={"this admin"}
        removeFunc={deleteAdminRole}
      />

      <AdminModal
        data={addAdmin.data}
        show={addAdmin.show}
        setAddAdmin={setAddAdmin}
        onClose={() => setAddAdmin({ show: false, data: null })}
      />
    </>
  );
}
