import React, { useEffect, useState } from "react";
import { Card, Form } from "react-bootstrap";
import { BiEdit } from "react-icons/bi";
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";
import { BiSearch } from "react-icons/bi";
import { RiDeleteBin6Fill } from "react-icons/ri";
import "react-datepicker/dist/react-datepicker.css";
import ReactDatePicker from "react-datepicker";
import { BsCalendarFill } from "react-icons/bs";
import "react-datepicker/dist/react-datepicker.css";
import { useDispatch, useSelector } from "react-redux";
import {
  deleteUser,
  getUserList,
} from "../../../app/features/userManagement/user.slice";
import FullScreenLoader from "../../../authorization/FullScreenLoader";
import StaffModal from "./StaffModal";
import { ConfirmationModal } from "../../../components/Modal/ConfirmationModal";
import moment from "moment";

export default function StaffRole({ setAddAdmin, addAdmin }) {
  const [open, setOpen] = useState(false);
  const [selectedDate, setSelectedDate] = useState(null);
  const [deleteId, setDeleteId] = useState();
  const [searchValue, setSearchValue] = useState();

  const selectRow = {
    mode: "checkbox",
  };
  const userList = useSelector((state) => state);
  const { isLoading } = useSelector((state) => state.userRole);

  const dispatch = useDispatch();

  useEffect(() => {
    const finalData = {
      roleId: 5,
      search: searchValue ? searchValue 
      : null,
      dob:  selectedDate ? moment(selectedDate).format("YYYY-MM-DD") : null,
    };
    dispatch(getUserList({ finalData }));
  }, [dispatch, searchValue, selectedDate]);

  function onDeleteSuccess() {
    let finalData = {
      userId: userList.auth.user.userId,
      roleId: 5,
    };
    dispatch(getUserList({ finalData }));
  }

  function deleteStaffRole() {
    const finalData = {
      userId: deleteId,
    };
    dispatch(deleteUser({ finalData, onDeleteSuccess }));
  }

  const Staffcolumns = [
    {
      dataField: "userId",
      text: "ID",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "name",
      text: "Staff Name",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1", width: "17%" },
    },
    {
      dataField: "email",
      text: "Email",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "phoneNumber",
      text: "Phone Number",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "createdDate",
      text: "Created Date",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
      formatter: (cell) => {
        const date = new Date(cell);
        const day = date.getDate().toString().padStart(2, "0");
        const month = (date.getMonth() + 1).toString().padStart(2, "0");
        const year = date.getFullYear();
        return `${month}/${day}/${year}`;
      },
    },
    {
      dataField: "action",
      text: "Action",
      sort: false,
      formatter: (row, cell) => {
        return (
          <>
            <BiEdit
              style={{ color: "#1A1A1A", cursor: "pointer" }}
              onClick={() => setAddAdmin({ data: cell, show: true })}
            />
            <RiDeleteBin6Fill
              style={{ color: "red", cursor: "pointer" }}
              className="ms-3"
              onClick={() => {
                setDeleteId(cell?.userId);
                setOpen(true);
              }}
            />
          </>
        );
      },
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
  ];
  const customTotal = (from, to, size) => (
    <span className="react-bootstrap-table-pagination-total">
      <b> {from} </b> to <b>{to}</b> out of <b>{size} entries</b>
    </span>
  );

  const paginateOptions = {
    paginationSize: 8,
    pageStartIndex: 1,
    alwaysShowAllBtns: true,
    withFirstAndLast: false,
    hideSizePerPage: true,
    showTotal: true,
    paginationTotalRenderer: customTotal,
    disablePageTitle: true,
    sizePerPageList: [
      {
        text: "10",
        value: 10,
      },
    ],
  };

  return (
    <>
      <Card className="user-details-card">
        <div className="px-4 pt-3 d-flex justify-content-between custom-row align-items-center table-header-border">
          <span className="d-flex align-self-center">
            <Form.Control
            onKeyDown={(e) => {
              if (e.key === "Enter") {
                setSearchValue(e.target.value);
              }
            }}
            onChange={(e) => {
              if (e.key === "Enter") {
                setSearchValue(e.target.value);
              }
            }}
              type="text"
              placeholder="Search"
              className="me-2 mb-3 search-field-spacing"
              style={{ width: "100%" }}
              aria-label="Search"
            />
            <BiSearch size={22} className="searchbar-icon" />
          </span>

          <div className="d-flex custom-div-row">
            <ReactDatePicker
              selected={selectedDate}
              onChange={(date) => setSelectedDate(date)}
              dateFormat="dd MMM yyyy"
              placeholderText="Date"
              className="custom-field-picker dr-date-w px-3 mb-3"
            />
            <BsCalendarFill
              className="custom-date-icon"
              size={18}
              style={{ right: "32px" }}
            />
          </div>
        </div>
        {/* {userList?.userRole?.isError === true && (
          <p className="text-center">Network Error...</p>
        )} */}
        {isLoading ? (
          <FullScreenLoader />
        ) : (
          <span className="doctor-datatable">
            <BootstrapTable
              columns={Staffcolumns}
              data={
                userList?.userRole?.getAllUser?.data
                  ? userList?.userRole?.getAllUser?.data
                  : []
              }
              keyField="userId"
              id="bar"
              pagination={paginationFactory(paginateOptions)}
              // striped
              bordered={false}
              wrapperClasses="table-responsive"
              selectRow={selectRow}
              sort={{ dataField: "userId", order: "desc" }}
            />
          </span>
        )}
      </Card>
      <ConfirmationModal
        show={open}
        onHide={() => setOpen(false)}
        heading={"Delete Staff"}
        title={"this staff"}
        removeFunc={deleteStaffRole}
      />
      <StaffModal
        data={addAdmin.data}
        show={addAdmin.show}
        onClose={() => setAddAdmin({ show: false, data: null })}
      />
    </>
  );
}
