import React, { useState } from "react";
import { Col, Row, Tab, Nav } from "react-bootstrap";
import "react-datepicker/dist/react-datepicker.css";
import { FaUser, FaUserMd } from "react-icons/fa";
import "react-datepicker/dist/react-datepicker.css";
import StaffRole from "./Staff";
import PatientRole from "./Patient";
import DoctorRole from "./Doctor";
import AdminRole from "./Admin";
import { useSelector } from "react-redux";
import { HiUserAdd } from "react-icons/hi";
import { ImUserCheck } from "react-icons/im";

export default function RoleManagement() {
  const [addAdmin, setAddAdmin] = useState({
    data: null,
    show: false,
  });
  const [key, setKey] = useState("admin");

  const { user } = useSelector((state) => state.auth);

  return (
    <div className="usermanagement-mainclass">
      <Tab.Container
        id="left-tabs-example"
        defaultActiveKey="admin"
        className="Admin-Tabs-SubMain"
        onSelect={(k) => setKey(k)}
      >
        <Row>
          <Col lg={9} md={9} sm={8}>
            <Nav variant="pills" className="flex-row Nav-MainAdmin">
              {user?.roleId === 1 && (
                <Nav.Item className="p-2 ps-0 ">
                  <Nav.Link
                    eventKey="admin"
                    style={{ background: "white", color: "#B3B3B3" }}
                    className="Admin-Tabs-All"
                  >
                    <FaUser className="me-2" /> Admin
                  </Nav.Link>
                </Nav.Item>
              )}

              <Nav.Item className="p-2 ps-0">
                <Nav.Link
                  eventKey="doctor"
                  style={{ background: "white", color: "#B3B3B3" }}
                  className="Admin-Tabs-All"
                >
                  <FaUserMd className="me-2" />
                  Doctor
                </Nav.Link>
              </Nav.Item>

              <Nav.Item className="p-2 ps-0">
                <Nav.Link
                  eventKey="patient"
                  style={{ background: "white", color: "#B3B3B3" }}
                  className="Admin-Tabs-All"
                >
                  <ImUserCheck className="me-2" />
                  Patient
                </Nav.Link>
              </Nav.Item>

              <Nav.Item className="p-2 ps-0">
                <Nav.Link
                  eventKey="staff"
                  style={{ background: "white", color: "#B3B3B3" }}
                  className="Admin-Tabs-All"
                >
                  <HiUserAdd className="me-2" size={20} />
                  Staff
                </Nav.Link>
              </Nav.Item>
            </Nav>
          </Col>
          <Col lg={3} md={3} sm={4}>
            <Tab.Content className="pt-0">
              <Tab.Pane eventKey="admin">
                <div className="d-flex justify-content-end ">
                  <button
                    className=" Add-Admin rounded"
                    onClick={() => setAddAdmin({ data: null, show: true })}
                  >
                    Add Admin
                  </button>
                </div>
              </Tab.Pane>
              <Tab.Pane eventKey="doctor">
                <div className="d-flex justify-content-end ">
                  <button
                    className=" Add-Admin rounded"
                    onClick={() => setAddAdmin({ data: null, show: true })}
                  >
                    Add Doctor
                  </button>
                </div>
              </Tab.Pane>
              <Tab.Pane eventKey="patient">
                <div className="d-flex justify-content-end ">
                  <button
                    className=" Add-Admin rounded"
                    onClick={() => setAddAdmin({ data: null, show: true })}
                  >
                    Add Patient
                  </button>
                </div>
              </Tab.Pane>
              <Tab.Pane eventKey="staff">
                <div className="d-flex justify-content-end ">
                  <button
                    className=" Add-Admin rounded"
                    onClick={() => setAddAdmin({ data: null, show: true })}
                  >
                    Add Staff
                  </button>
                </div>
              </Tab.Pane>
            </Tab.Content>
          </Col>

          <Col lg={12} md={12}>
            <Tab.Content>
              <Tab.Pane eventKey="admin">
                {key === "admin" && (
                  <AdminRole addAdmin={addAdmin} setAddAdmin={setAddAdmin} />
                )}
              </Tab.Pane>
              <Tab.Pane eventKey="doctor">
                {key === "doctor" && (
                  <DoctorRole addAdmin={addAdmin} setAddAdmin={setAddAdmin} />
                )}
              </Tab.Pane>

              <Tab.Pane eventKey="patient">
                {key === "patient" && (
                  <PatientRole addAdmin={addAdmin} setAddAdmin={setAddAdmin} />
                )}
              </Tab.Pane>

              <Tab.Pane eventKey="staff">
                {key === "staff" && (
                  <StaffRole addAdmin={addAdmin} setAddAdmin={setAddAdmin} />
                )}
              </Tab.Pane>
            </Tab.Content>
          </Col>
        </Row>
      </Tab.Container>
    </div>
  );
}
